.pipeContainer{display:flex;height:calc(100vh - 100px);-webkit-margin-before:0;margin-block-start:0;-webkit-padding-start:0;padding-inline-start:0}.header{display:flex;flex-direction:row;text-align:center;background-color:#232c36;color:#fff;margin:8px 0;border-radius:5px}.header .actions{align-self:center;margin:8px}.header .actions .actionContainer{border-radius:5px;background-color:#2c3a46;transition:transform .5s;padding:8px 16px}.header .actions .actionContainer i{font-size:18px}.header .actions .actionContainer:hover{transform:translateY(-2px);background-color:#414e59}.header .pipeTitle{flex-grow:1}.header .pipeTitle div:first-child{font-size:20px;color:#949ca0}.header .pipeTitle div:last-child{font-variant-caps:all-petite-caps;font-size:24px;line-height:30px;margin-top:-8px}