.modalWrap>div{width:800px}.modalWrap>div>div{background-color:hsla(0,0%,100%,0);box-shadow:none}.modalDiv{width:450px;box-shadow:0 4px 12px rgba(0,0,0,.15);border-radius:10px;border-top:20px solid #f5f7fb;background-color:#fff}.leftSideModal{height:600px;background-color:#ebf0f7}.leftSideModal .headerTitle{font-size:18px;color:#0094c6;font-weight:700}.leftSideModal .headerDescription{font-size:14px;color:#505f6f;font-weight:600}.modalBodyContainer{padding:16px 32px 32px;display:flex;flex-direction:column;height:100%}.modalBodyContainer .modalBody{flex-grow:1}.modalBodyContainer header{margin-top:-13px;display:flex;flex-direction:column}.rightSideModal{box-shadow:-4 -4px 12px rgba(0,0,0,.15);height:500px;position:relative;top:30px;border-bottom-left-radius:0;border-bottom-right-radius:0;left:-30px}.rightSideModal .headerTitle{font-size:18px;color:#1e252e;font-weight:600}.rightSideModal .headerDescription{font-size:14px;color:#949ca0;font-weight:600}.rightSideModal .modalFooter{border-bottom-left-radius:10px;border-bottom-right-radius:10px;background-color:#0094c6;display:flex;align-items:center;height:60px}.rightSideModal .modalFooter div{flex:auto;color:#fff;text-align:center;font-size:18px;font-weight:600;transition:font-size .3s}.rightSideModal .modalFooter:hover{background-color:#199fcc;cursor:pointer}.rightSideModal .modalFooter:hover div{font-size:22px}