.card{display:flex;flex-direction:column;width:100%;margin-top:16px;padding:8px 16px;background-color:#fff;border-radius:5px;box-shadow:2px 2px 8px hsla(0,0%,80.8%,.26);cursor:pointer}.card .cardHeader{display:flex;flex-direction:row;align-items:center}.card .cardHeader .moveArrow{flex-grow:0;margin-right:10px;font-size:16px}.card .cardHeader .title{display:grid;flex-grow:5}.card .cardHeader .title .cardCNPJ{color:#949ca0;font-size:12px;font-family:cursive}.card .cardHeader .title .cardName{font-weight:700;font-size:14px;font-family:cursive}.card .cardHeader .showMore{width:26px;margin-left:4px;font-size:26px}