.inputContainer{display:flex;flex-direction:column;margin-bottom:8px;padding:0 8px}.inputContainer .input{border:none;outline:none;font-size:14px;color:#0094c6}.inputContainer .input:focus{background-color:#ebf0f7;height:24px;padding:8px;border-radius:5px}.inputContainer .label{color:#505f6f;font-size:14px;font-weight:600}.inputContainer .actionContainer{display:flex;flex-direction:row;padding:8px 16px}.inputContainer .actionContainer .actionButton{height:32px;width:50%;background-color:#0094c6;margin:0 16px;padding:8px;border-radius:5px;display:flex;align-items:center}.inputContainer .actionContainer .actionButton span{text-align:center;width:100%;font-weight:600;color:#fff}.inputContainer .actionContainer .actionButton:hover{background-color:rgba(0,148,198,.8)}.inputContainer:hover{cursor:pointer;border:1px solid #ebf0f7;border-radius:5px;padding:-2px 6px}